import { Component, OnInit, Input, OnChanges } from '@angular/core';
import { CusOperationHttpService } from './../../../http/custom-operation/custom-operation.http.service'
import { GroupMarketPlanBean } from './../../bean/custom-group.bean'
import { Commfunc } from 'app/@uisftech/common/service/commonFun';
import * as _ from 'lodash';
import { SelectItem, Message, ConfirmationService } from 'primeng/primeng';
import { ActivatedRoute, Params, Router } from '@angular/router';
import { CALENDAR_CN } from 'app/@uisftech/common/constant/config';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { CommonHttpService } from "app/pages/tzb/common/http/common.http.service";
import { TZB_HTTP_SX } from "app/@uisftech/common/service/http.constant";
import { TenTypeBean } from './bean/tenType.bean';

@Component({
  selector: 'group-marketing-plan',
  templateUrl: './group-marketing-plan.component.html',
  styleUrls: ['./group-marketing-plan.component.scss'],
  providers: [Commfunc, ConfirmationService],
})
export class GroupMarketingPlanComponent implements OnInit, OnChanges {
  // @Input() groupId: any;
  // @Input() groupName: any;
  // @Input() groupListDemo: any;
  groupId: any;
  groupName: any;
  // groupListDemo: any;

  // @Input() groupOwnerflag: any;

  //公共
  tableMesg: any = tableMessage;
  customGroup: string = "XXX客户群";
  marketSchemeSearch: string = "营销方案历史记录";
  arr: any;
  cars: any;
  groupMarketPlanBean: GroupMarketPlanBean = new GroupMarketPlanBean();// 客户群营销方案
  tenTypeBean: TenTypeBean = new TenTypeBean();//token
  startDate: string = '';
  endDate: string = '';

  // 中间量
  groupIdTemp: string = '';//群号
  groupNameTemp: string = '';//群名称
  planId: string = '';//营销方案名
  statusId: string = '';//状态
  creator: string = '';//创建人
  creater_arr: any;

  dateNow1: any = '';
  dateNow: any = '';
  // 控件
  creater: any;
  msgs: Message[] = [];
  first: any = 0;
  ch: any = CALENDAR_CN;
  user: any;
  user2: any;
  orgId: string = '';
  userId: string = '';
  userName: string = '';
  orgName: string = '';
  //获取所有码值
  code: any;
  state: any;// = GroupStatusId;//状态下拉框
  display: boolean = false;//模态框
  constructor(
    public httpService: CusOperationHttpService,
    public commfunc: Commfunc,
    public routerInfo: ActivatedRoute,
    public router: Router,
    public confirmationService: ConfirmationService,
    public CommonHttpService: CommonHttpService
  ) {
    this.codeValues() //调用方法，获取全部码值
    //获取所需要的码值,中括号中的变量是自己在原来码值表中的变量名！！！！
    this.state = this.code['GroupStatusId'];
    this.user = JSON.parse(this.commfunc.getSessionData('commonHeader'));
    this.user2 = JSON.parse(this.commfunc.getSessionDataCH('chName'));
    if (this.user.orgId) {
      this.orgId = this.user.orgId;
    }
    if (this.user.userId) {
      this.userId = this.user.userId;
    }
    if (this.user2.userName) {
      this.userName = this.user2.userName;
    }
    if (this.user2.orgName) {
      this.orgName = this.user2.orgName;
    }

    //路由获取参数 
    this.groupId = this.routerInfo.snapshot.params['groupId'] ? this.routerInfo.snapshot.params['groupId'] : JSON.parse(this.routerInfo.snapshot.params['dataMap'])['custId'];

  }
  //码值
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code是自己声明的变量
  }

  ngOnInit() {
    this.preSet();

  }

  ngOnChanges() {

  }

  preSet() {
    this.selectGroupMarketPlanCreator();
    // 初始化
    this.dateNow = new Date();
    this.dateNow1 = this.commfunc.transDate(this.dateNow);
    if (this.groupId) {
      this.groupIdTemp = this.groupId;
      let params = {
        groupId: this.groupId
      }
      this.httpService.selectCustGroup(params).subscribe(data => {
        if (data.returnCode.code == 'success') {
          this.groupNameTemp = data.groupList[0].groupName;

          this.groupMarketPlanBean.groupId = this.groupIdTemp;//群号
          this.groupMarketPlanBean.groupName = this.groupNameTemp;//群名称
          this.groupMarketPlanBean.pageNum = 1;
          this.groupMarketPlanBean.pageSize = 10;
          this.groupMarketPlanBean.statusId = '';
          this.first = 0;
          this.select();
          this.msgs = [];
          this.msgs = [{ severity: 'success', summary: '提示', detail: '查询成功！' }];
        }
        else {
          this.msgs = [];
          data.returnCode.message ? data.returnCode.message : '查询失败！';
          this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
        }
      }, err => {
        this.msgs = [];
        this.msgs = [{ severity: 'error', summary: '提示', detail: '调用服务失败！' }];
      })
    }
  }

  reSet() {
    this.groupMarketPlanBean = new GroupMarketPlanBean();// 客户群营销方案
    this.startDate = '';
    this.endDate = '';
    // 中间量
    this.groupIdTemp = '';//群号
    this.groupNameTemp = '';//群名称
    this.planId = '';//营销方案名
    this.statusId = '';//状态
    this.creator = '';//创建人
    this.dateNow1 = '';
    this.dateNow = '';
    this.creater = [];
    this.first = 0;
  }

  select(flag?) {
    // debugger
    // 点击查询
    this.groupMarketPlanBean.groupId = _.clone(this.groupIdTemp);//群号
    this.groupMarketPlanBean.groupName = _.clone(this.groupNameTemp);//群名称
    this.groupMarketPlanBean.planId = _.clone(this.planId);//营销方案名
    this.groupMarketPlanBean.statusId = _.clone(this.statusId);//状态
    this.groupMarketPlanBean.creator = _.clone(this.creator);//创建人
    this.groupMarketPlanBean.startDate = _.clone(this.startDate);//开始时间绑定
    this.groupMarketPlanBean.endDate = _.clone(this.endDate);  //结束时间绑定
    let param = _.clone(this.groupMarketPlanBean);
    if (!this.groupMarketPlanBean.groupId || this.groupMarketPlanBean.groupId == '' || this.groupMarketPlanBean.groupId.search(/^\S+$/)) {
      // 非空校验
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '客户群号不能为空！' });
      return;
    }

    // 时间默认

    if (!this.groupMarketPlanBean.startDate || this.groupMarketPlanBean.startDate == '') {
      param.startDate = '10001010';//默认开始时间
    }
    else {
      param.startDate = this.commfunc.transDate(param.startDate);//选择开始时间
      if (param.startDate > this.dateNow1) {
        this.msgs = [];
        this.msgs.push({ severity: 'info', summary: '提示', detail: '开始时间默认为当前日期！' });
        this.startDate = this.dateNow;
        this.groupMarketPlanBean.startDate = _.clone(this.startDate);  //结束时间绑定
        param.startDate = this.dateNow1;
      }
    }
    if (!this.groupMarketPlanBean.endDate || this.groupMarketPlanBean.endDate == '') {
      param.endDate = this.dateNow1;//默认结束时间
    }
    else {
      param.endDate = this.commfunc.transDate(param.endDate);//选择结束时间
      if (param.endDate > this.dateNow1) {
        this.msgs = [];
        this.msgs.push({ severity: 'info', summary: '提示', detail: '结束时间默认为当前日期！' });
        this.endDate = this.dateNow;
        this.groupMarketPlanBean.endDate = _.clone(this.endDate);  //结束时间绑定
        param.endDate = this.dateNow1;
      }
    }
    if (param.startDate > param.endDate) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '开始时间不能大于结束时间！' });
      return;
    }
    this.groupMarketPlanBean = param;
    this.selGroupMarketPlan(flag);
  }

  // 客户群营销方案查询
  selGroupMarketPlan(flag?) {
    if (flag == '1') {
      this.groupMarketPlanBean.pageNum = 1;
    }
    this.httpService.selectGroupMarketPlan(this.groupMarketPlanBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.groupMarketPlanBean = data;
        this.groupMarketPlanBean.planList = data.planList;
        if (this.groupMarketPlanBean.planList) {
          this.groupMarketPlanBean.planList.forEach((item, i) => {
            this.state.forEach(item_2 => {
              if (item_2.value == item.statusId) {
                this.groupMarketPlanBean.planList[i]["statusName"] = item_2.label;
              }
            });
          })
        }
        this.groupMarketPlanBean.planList.forEach((element, i) => {
          this.queryTellerByCombConditions(element.creator, i);
        });
      }
      else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '查询失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  queryTellerByCombConditions(createrId, i) {
    let params = {
      tellerId: createrId
    }
    this.httpService.queryTellerByCombConditions(params).subscribe(data => {
      if (data.returnCode.code == 'success') {
        // console.log(data);
        data.tellerList.forEach(element => {
          if (element.tellerId == this.groupMarketPlanBean.planList[i].creator) {
            this.groupMarketPlanBean.planList[i].creator = element.tellerName;
          }
        });
      }
      else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '查询失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.message }];
      }
    }, err => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }

  shoGroupMarketPlanDetail(col) {
    let data = {
      planId: col.planId,
      statusId: col.statusId,
      groupId: this.groupIdTemp,
      groupName: this.groupNameTemp,
    }
    this.router.navigate(['/pages/tzb/custom/custom-group/market-scheme', data]);
  }
  add() {
    this.confirmationService.confirm({
      message: '跳转到新增页面？',
      accept: () => {
        this.router.navigate(['/pages/tzb/custom/custom-group/create-group-marketing', { groupId: this.groupId, groupName: this.groupNameTemp }]);
      }
    });
  }
  //客户细分
  tion() {
    this.display = true;
  }
  closeAdd(event) {

  }

  paginate(event) {
    // 分页
    this.groupMarketPlanBean.pageSize = event.rows;
    let currentPage = event.page + 1;
    this.groupMarketPlanBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.groupMarketPlanBean.pageSize;
    this.selGroupMarketPlan();//客户群营销方案查询
  }
  //按钮权限
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }
  //人员查询
  selectGroupMarketPlanCreator() {
    let param = {
      groupId: this.groupId
    }
    this.httpService.selectGroupMarketPlanCreator(param).subscribe(data => {
      if (data.returnCode.code == 'success') {
        this.creater_arr = [{ label: '请选择', value: '' }];
        data.infoList.forEach(item => {
          this.queryTellerById(item);
        });
      }
      else {
        this.msgs = [];
        data.returnCode.code ? data.returnCode.code : '调用服务失败！';
        this.msgs = [{ severity: 'error', summary: '提示', detail: data.returnCode.code }];
      }
    }, err => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败！' });
    })
  }
  //查询人员姓名selectGroupMarketPlan
  queryTellerById(item) {
    let params = {
      tellerId: item.creator
    }
    let successFnc = data => {
      this.creater_arr.push({ label: data.tellerName, value: item.creator })
    }
    this.commfunc.interfaceComFnc(this.CommonHttpService.queryTellerById(params), successFnc, this.msgs)
  }
  //营销方案历史
  historyMarketing() {
    let timestamp = new Date().getTime();
    let target = "custLinkForm" + timestamp;
    // this.commfunc.createBlank(target);
    this.tenTypeBean.userId = this.userId;
    this.tenTypeBean.sysCode = '122';
    this.tenTypeBean.casServerLoginUrl = 'http://10.1.39.140:8011/searchPaymentTool';
    this.tenTypeBean.serverName = 'ywsn';
    this.tenTypeBean.validateType = '0';
    this.tenTypeBean.extraInfo = 'ywsn';
    this.tenTypeBean.encryptType = 'HS256';
    this.httpService.tokenApply(this.tenTypeBean).subscribe(data => {
      if (data.returnCode.code == 'success') {
        let tokens = data.tokenCode;
        let url = TZB_HTTP_SX + "ifs/visiturl.do?token_id=" + tokens + "&comg_no=" + this.groupId + "&tranFlag=yxfa&user_id=" + this.userId + "&domain_name=ywsn&url=http://10.1.39.140:8011/searchPaymentTool&bankCode=BR00A";
        let features = 'top=80,left=50,width=1180,height=768,toolbar=no,location=no,directories=0,status=1,menubar=no,scrollbars=yes,resizable=yes';
        let closeOnLoseFocus = 'closeOnLoseFocus';
        this.commfunc.openPostWindow(url, "custLinkForm" + timestamp, features, closeOnLoseFocus);
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: data.returnCode.message });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '提示', detail: '调用服务失败' });
    })
  }
}


